.btn {
  position: relative;
  overflow: hidden;
  z-index: 1;
  transition: all 0.3s ease;
  font-size: clamp(0.75rem, 1vw, 1rem);
  border-radius: 0;
}
.btn:before {
  display: block;
  position: absolute;
  left: 0px;
  top: 0px;
  height: 0px;
  width: 100%;
  z-index: -1;
  content: "";
  background: #000000;
  transition: all 0.4s cubic-bezier(0.215, 0.61, 0.355, 1) 0s;
}
.btn:hover::before {
  bottom: 0%;
  top: auto;
  height: 100%;
}
.btn:focus {
  box-shadow: none;
}
.btn.btn-primary {
  border: 0;
  background-color: #A5BBD4;
  color: #ffffff;
  border-color: transparent;
}
.btn.btn-primary:hover {
  color: #A5BBD4;
}
.btn.btn-secondary {
  border: 0;
  background-color: #2E2E2E;
  color: #ffffff;
  border-color: transparent;
}
.btn.btn-secondary:before {
  background: #A5BBD4;
}
.btn.btn-secondary:hover {
  color: #2E2E2E;
}

.back-to-top {
  position: fixed;
  width: 44px;
  height: 44px;
  z-index: 999;
  bottom: 5rem;
  right: 20px;
  display: none;
}

.btn.has-down-arrow {
  position: relative;
}
.btn.has-down-arrow[aria-expanded=true]::after {
  content: "";
  width: 0;
  height: 0;
  border-left: 17px solid transparent;
  border-right: 17px solid transparent;
  border-bottom: 17px solid rgba(219, 198, 163, 0.5);
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

